#header-main_gallery {
  width: 100%;
  background-color: #00003b;
  margin-top: 0;
  margin-bottom: 24px;
  padding: 0 20px;
}

.gallery_nav {
  height: 60px;
  max-width: 1200px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.gallery_nav ul {
  gap: 14px;
  list-style: none;
  display: flex;
}

.gallery_nav ul a {
  color: #fff;
  transition: color var(--animation-duration) var(--timing-function);
  font-weight: 500;
  text-decoration: none;
}

.gallery_nav ul a:hover, .gallery_nav ul a:focus {
  color: orange;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 999;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  transition: opacity .35s ease-in-out, visibility .35s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

.backdrop_close_btn {
  z-index: 10;
  width: 38px;
  height: 38px;
  color: rgba(0, 0, 0, .6);
  transition: background-color var(--animation-duration) var(--timing-function), color var(--animation-duration) var(--timing-function), transform 50ms var(--timing-function);
  background-color: rgba(255, 255, 255, .8);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 26px;
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
  transform: scale(1);
}

.backdrop_close_btn:hover, .backdrop_close_btn:focus {
  color: rgba(255, 255, 255, .8);
  background-color: rgba(0, 0, 0, .6);
}

.backdrop_close_btn:active {
  transform: scale(.8);
}

.arrow_left {
  z-index: 10;
  color: rgba(255, 255, 255, .8);
  transition: color var(--animation-duration) var(--timing-function), transform 50ms var(--timing-function);
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  font-size: 56px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 24px;
  transform: translateY(-50%)scale(1);
}

.arrow_right {
  z-index: 10;
  color: rgba(255, 255, 255, .8);
  transition: color var(--animation-duration) var(--timing-function), transform 50ms var(--timing-function);
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  font-size: 56px;
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 24px;
  transform: translateY(-50%)scale(1);
}

.arrow_left:hover {
  color: rgba(0, 0, 0, .6);
}

.arrow_left:active {
  transform: translateY(-50%)scale(.8);
}

.arrow_right:hover {
  color: rgba(0, 0, 0, .6);
}

.arrow_right:active {
  transform: translateY(-50%)scale(.8);
}

.animate-left-out {
  animation: slideOutLeft .5s forwards;
}

@keyframes slideOutLeft {
  0% {
    opacity: 1;
    transform: translateX(0);
  }

  100% {
    opacity: 0;
    transform: translateX(-100%);
  }
}

.animate-right-out {
  animation: slideOutRight .5s forwards;
}

@keyframes slideOutRight {
  0% {
    opacity: 1;
    transform: translateX(0);
  }

  100% {
    opacity: 0;
    transform: translateX(100%);
  }
}

.animate-left-in {
  animation: slideInLeft .5s forwards;
}

@keyframes slideInLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

.animate-right-in {
  animation: slideInRight .5s forwards;
}

@keyframes slideInRight {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

